.container {
  position: relative;
}

.selectEl {
  width: 500px;
  gap: 40px;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.ss-main {
  height: 45px;
}

.showcase {
  width: 100px;
  height: 100px;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  display: flex;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

.loader_cat {
  color: #00f;
  width: 1em;
  height: 1em;
  text-indent: -9999em;
  border-radius: 50%;
  font-size: 10px;
  animation: mulShdSpin 1.3s linear infinite;
  position: relative;
  top: 60px;
  transform: translateZ(0);
}

@keyframes mulShdSpin {
  0%, 100% {
    box-shadow: 0 -3em 0 .2em, 2em -2em 0 0, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em;
  }

  12.5% {
    box-shadow: 0 -3em, 2em -2em 0 .2em, 3em 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }

  25% {
    box-shadow: 0 -3em 0 -.5em, 2em -2em, 3em 0 0 .2em, 2em 2em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }

  37.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0, 2em 2em 0 .2em, 0 3em 0 0, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }

  50% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0, 0 3em 0 .2em, -2em 2em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }

  62.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em, -2em 2em 0 .2em, -3em 0, -2em -2em 0 -1em;
  }

  75% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em, -3em 0 0 .2em, -2em -2em;
  }

  87.5% {
    box-shadow: 0 -3em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em, -3em 0, -2em -2em 0 .2em;
  }
}

.loader, .loader:before, .loader:after {
  width: 2.5em;
  height: 2.5em;
  border-radius: 50%;
  animation: bblFadInOut 1.8s ease-in-out infinite;
}

.loader {
  color: #00f;
  text-indent: -9999em;
  font-size: 7px;
  animation-delay: -.16s;
  position: relative;
  transform: translateZ(0);
}

.loader:before, .loader:after {
  content: "";
  position: absolute;
  top: 0;
}

.loader:before {
  animation-delay: -.32s;
  left: -3.5em;
}

.loader:after {
  left: 3.5em;
}

@keyframes bblFadInOut {
  0%, 80%, 100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }

  40% {
    box-shadow: 0 2.5em;
  }
}

.is-hidden {
  display: none;
}

.cat-container {
  justify-content: center;
  gap: 50px;
  margin-top: 140px;
  display: flex;
}

.cat {
  width: 500px;
}

.cat-name {
  font-size: 38px;
}

.cat-description {
  font-size: 18px;
}

.cat-temperament {
  font-size: 20px;
}

.cat-origin {
  font-size: 24px;
}

.cat-container img {
  cursor: zoom-in;
}

.btn-refresh {
  cursor: pointer;
  border-radius: 6px;
  font-size: 16px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.btn-refresh:hover {
  background-color: #7fffd4;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

